*{
	padding: 0;

}
body{
	font-family: Galeria Bold;
	background-color: #f4f4f4;
}
.logo{
	display: inline-block;
	font-size: 40px;
	margin-left: 5%;
	font-family: Galeria light;
	margin-top: 10px;
	transition: .7s ease;
}
.navbar{
	border-bottom: 6px solid #1c1c1c;
	overflow: hidden;
	min-height: 70px;
	display: block;
}
.navbar button{
	float: right;
	display: block;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
	font-size: 40px;
	margin-right: 20px;
	color: #000;
}
.navbar a:hover{
	color: red;
}
.side-nav{
	height: 100vh;
	width: 0;
	position: fixed;
	z-index: 1;
	top: 0;
	right: 0;
	background-color: #5987aa;
}
.side-nav a{
	padding: 10px 10px 10px 30px;
	text-decoration: none;
	font-size: 90px;
	color: green;
	display: block;
	transition: 0.3s;
	text-align: right;
	position: relative;
}
.side-nav span{
	position: relative;
}
.side-nav span button {
	position: absolute;
	font-size: 60px;
	top: -70px;
	transition: .1s;
}
.ventana , ventana2 {
	color: blue;
	display: inline-flex;
	width: 420px;
	height: 400px;
	position: absolute;
	bottom: 300px;
	margin-left: 103px;
}
.ventana{
	background: green;
}
.ventana2{
	bottom: 285px;
	margin-left: 90px;
	z-index: inherit;
	border: 6px solide #1c1c1c;
}
#logo{
	font-size: 30px;
	margin-top: 16px;
	margin-left: 2%;
}
#side-menu{
	width:150px;
}
#close-side{
	visibility: hidden;
}
#titulos{
	visibility: hidden;
	transition: .1s;
}
.active{
	visibility:visible;
	width:66% !important;
